@import url("https://use.typekit.net/xbd0ekf.css");

html, body {
  background-color: black;
  margin: 0;
  padding: 0;
}

.App {
  display: flex; /* Use flexbox to align child divs */
  justify-content: space-between; /* Distribute divs equidistant */
  align-items: center; /* Vertically center items */
  height: 16.9vw; /* Full viewport height */
  width: 100vw; /* Full viewport width */
  overflow: hidden;
  background: linear-gradient(268deg, #00ee3f, #00c8ff, #ffc000, #ff194f, #ff00ff, #6900d3, #ff0000, #ff9d00, #ffc400, #00ff26, #00ff9d);
  background-size: 800% 1200%;
  -webkit-animation: AnimationName 60s ease infinite;
  -moz-animation: AnimationName 60s ease infinite;
  animation: AnimationName 60s ease infinite;
}

@keyframes AnimationName {
  0% {
    background-position: 7% 0%;
  }
  50% {
    background-position: 94% 100%;
  }
  100% {
    background-position: 7% 0%;
  }
}

.theArt, .theArt2, .theArt3, .theArt4, .theArt5, .theArt6 {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 15px;  /* Starting margin for the first element */
  cursor: pointer;
}

.theArt2 {
  margin-left: 30px;
  cursor: pointer;
}

.theArt3 {
  margin-left: 5px;
  cursor: pointer;
}

.theArt4 {
  margin-left: 40px;
  cursor: pointer;
}

.theArt5 {
  margin-left: 10px;
  cursor: pointer;
}

.theArt6 {
  margin-left: 35px;
  cursor: pointer;
}

.theWords {
  position: relative;
  z-index: 15;
  color: transparent;
  font-size: 50pt;
  font-family: "cofo-sans-pixel", sans-serif;
  font-weight: 400;
  font-style: normal;
  padding-top: 1200px;
  text-align: center;
}

.theWords:hover {
  cursor: pointer;
}
